@font-face{
  font-family:'bitbit';
  src:url('//cdn.df.nexon.com/img/common/font/DNFBitBit-Regular.woff'),url('//cdn.df.nexon.com/img/common/font/DNFBitBit-Regular.woff2') ;
}

@font-face {
  font-family: 'DungGeunMo-Regular';
  src: url('https://cdn.jsdelivr.net/gh/wooin21/web/fonts/etc/DungGeunMo-Regular.woff');
  font-weight: normal;
  font-style: normal;
}

.mypage-total-wrapper{
  width: 100%;
  height: 515px;

  display: flex;

}

.mypage-coding-calendar-wrapper{
  margin-top: 1.5%;
  margin-bottom: 1.5%;

  width: 30%;
  height: auto;
}

.mypage-coding-calendar-background{
  margin-left: 5%;

  display: flex;
  align-items: center;
  justify-content: center;

  background-color: white;

  width: 100%;
  height: 470px;
}

.mypage-right-space-wrapper{
  display: flex;
  flex-direction: column;

  margin-top: 1.5%;
  margin-bottom: 1%;
  margin-left: 2.5%;

  width: 66%;
}
/*Coding Badge Information*/
.mypage-codingBadge-info-wrapper{
  width:100%;
  height: 230px;

  margin-bottom: 1.1%;
}

.mypage-codingBadge-info-background{
  background-color: white;

  width:100%;
  height: 230px;
}

.mypage-codingBadge-info-contents{
  display: flex;

  width:100%;
  height: auto;

  padding: 10px;
}

.mypage-codingBadge-info-img-box{
  width: 204px;
  height: 204px;

  border: 3px solid #C8B3D3 ;
  border-radius: 10%;

  margin-right: 5%;
}

.mypage-codingBadge-info-img{
  width: 210px;
  height: 210px;

  /* background-image: url('../../public/images/codingBadge.png'); */
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
.mypage-codingBadge-info-img img{
  margin-left:2%;
  margin-top:2%;
  width:80%;
  height:80%;
}
.mypage-codingBadge-info-box{
  width:70%;
  height: auto;
}

.mypage-codingBadge-info-text-badge-name{
  display: block;

  margin-bottom: 40px;

  font-family: 'DungGeunMo-Regular';
  font-size: 36px;
  font-weight: 500;
}

.mypage-codingBadge-info-text-exp{
  display: block;
  font-family: 'DungGeunMo-Regular';
  font-size: 22px;
}

.mypage-codingBadge-info-text-remain-exp{
  display: block;
  font-family: 'DungGeunMo-Regular';
  font-size: 22px;
}

.mypage-codingBadge-info-exp-box{
  width: 100%;
  height: 40px;

  position:relative;

  margin-top: 40px;

  border: 2px solid #000000;
}

.mypage-codingBadge-info-exp-gage{
  position:absolute;

  width: 38.4%;
  height: 40px;

  background-color: #C8B3D3;

  text-align: center;
  line-height: 36px;

  font-weight: 700;
}

.mypage-recommend-question-wrapper{
  width:100%;
  height: 230px;
}

.mypage-recommend-question-background{
  background-color: white;

  width:100%;
  height: 230px;
}

.mypage-recommend-question-contents{
  font-family: 'DungGeunMo-Regular';
  
  padding: 10px;
}

.mypage-recommend-question-title{
  display: block;
  text-align: center;

  font-size: 22px;
}

/*Calendar*/
.mypage-current-calendar{
width: 100%;
height: 100%;
}

.mypage-current-calendar .react-calendar{
width: 100%;
height: 100%;

font-family: "DungGeunMo-Regular";
font-size: 20px;
}

.mypage-current-calendar .react-calendar .react-calendar__navigation {
  background: #FBD9D7;
  border-bottom: 4px solid #E8BDC7;
  height: 60px;
  margin-bottom: 10px;

  
    font-size: 24px;
    font-weight: 600;
    color: #E8BDC7;
 
}

.react-calendar__navigation button:disabled {
  background-color: #FBD9D7;

}

.react-calendar__navigation button:enabled:hover,
.react-calendar__navigation button:enabled:focus {
  background-color: #FBD9D7;
}

.react-calendar__month-view {
  padding: 12px 10px;

    color: #E8BDC7;
    font-size: 18px;
    font-weight: 500;
  }

.react-calendar__month-view__weekdays {

    font-size: 19.3px;
    font-weight: 900;
  
}

.mypage-current-calendar .react-calendar__tile {
  text-align: center;
  height: 58px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding: 0;
}

/*hover, focus,   */
.react-calendar__tile:enabled:hover,
.react-calendar__tile:enabled:focus,
.react-calendar__tile--active {
  border-radius: 14px;
}

.react-calendar__tile--now {
  border-radius: 14px;
}

/*hover, focus  */
.react-calendar__tile--now:enabled:hover,
.react-calendar__tile--now:enabled:focus {
  border-radius: 14px;
}

.react-calendar__navigation button{
  font-family: "DungGeunMo-Regular";
  font-size: 28px;
  font-weight: bold;
}

/*  */
.mypage-recommend-question-contents-wrapper{
  width: 100%;
  height: 200px;
  display: flex;
  font-size:42px;
  justify-content: space-around;
  align-items: center;
}
.recommend-user-wrapper{
  width: 150px;
  height: 170px;

  background-color: #ffffff;
  border-radius: 20px;
  border: #C8B3D3 5px solid;

  display: flex;
  flex-direction: column;
  align-items: center;
}

.recommend-user-img{
  width: 120px;
  height: 120px;

  background-image: url('../../public/images/codingBadge.png');
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  border-bottom: #C8B3D3 2px solid;

  margin-bottom: 10px;
}
.recommend-user-title{
text-overflow: ellipsis;
}
